 *
 * Replace them with your own styles or remove the file completely.
 */


 .wp-block-slider-news__post-thumbnail {
    width: 100%;
}


 .wp-block-create-block-slider-news {
	.slider {
		display: flex;
		overflow-x: auto;
		overflow-y: hidden;
		scrollbar-color: #f3f1e9 #fa4616;
		padding-left: 20px;
		padding-right: 20px;
		margin-bottom: 20px;
	  
		@media only screen and (min-width: 768px) {
		  margin-bottom: 50px;
		}
	  
	  
		.slider-col {
		  max-width: 450px;
		  transition: all 0.2s ease-in-out;
		  min-width: 300px;
		  text-decoration: none;
		  width: 100%;
	  
		  &:hover,
		  &:focus {
			background-color: #f3f1e9;
		  }
	  
		  .slider-content-wrapper {
			position: relative;
			z-index: 999;
		  }
	  
		  .slide-title {
			font-family: "IBM Plex semibold";
			font-size: 24px;
			line-height: 28px;
			text-align: left;
			color: #000;
		  }
	  
		  .slide-subtext {
			font-family: "IBM Plex light";
			font-size: 20px;
			line-height: 28px;
			text-align: left;
			color: #000;
		  }
		}
	  }
	  
	  .slider::-webkit-scrollbar {
		height: 1rem;
		-webkit-appearance: none;
	  }
	  
	  .slider::-webkit-scrollbar-track {
		background-color: #bbbbba;
		border-radius: 15px;
		margin: 60px;
	  
		@media only screen and (min-width: 768px) {
		  margin: 30vw;
		}
	  }
	  
	  .slider::-webkit-scrollbar-thumb {
		border-radius: 15px;
		background-color: #bbbbba;
		background-image: linear-gradient(
		  #bbbbba 0,
		  #bbbbba 0rem,
		  #fa4616 0rem,
		  #fa4616 1rem
		);
	  }
	  
	  
	  
	  
	  .mobile-slide-btn {
		display: flex;
		justify-content: center;
		font-size: 42px;
		gap: 12px;
		align-items: center;
	   margin-bottom: 20px;
	  
		@media only screen and (min-width: 768px) {
		  display: none;
		}
	  
	  
		button {
		  appearance: none;
		  -webkit-appearance: none;
		  background: none !important;
		  padding-left: 10px;
		  padding-right: 10px;
		  color: #FA4616 !important;
		  border: none;
		  line-height: 1;
		}
	  
	  }
	
	  .slider-content-wrapper img {
		max-width: 100%;
		height: auto;
	}
}
